* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montserrat', sans-serif;
}

.app {
  background-image: url('./assets/cold-weather.jpeg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
}

main {
  height: 100vh;
  padding: 25px;
}

.app.warm {
  background-image: url('./assets/warm-weather.jpeg');
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  width: 100%;
  padding: 15px;
  display: block;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  color: #313131;
  font-size: 20px;
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  box-shadow: 0 5px rgba(0,0,0,0.2);
  transition: 0.4 ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255,255,255, 0.75);
}

.location-box .location {
  color: white;
  font-weight: 500;
  text-align: center;
  font-size: 32px;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.location-box .date {
  color: white;
  font-size: 20px;
  font-style: italic;
  text-align: center;
  font-weight: 300;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
}

.weather-box {
  text-align: center;

}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: white;
  font-size: 102px;
  text-shadow: 3px 6px rgba(50,50,70,0.5 );
  font-weight: 900;
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather {
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.highlow {
  text-align: center;
  color: white;
  margin: 0 auto;
}
.high, .low {
  display: inline-block;
  padding: 5px 30px;
  font-size: 24px;
}
